.Button{
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    color: #fff;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    cursor: pointer;
}


.Button:focus{
    outline: none;
}

.Button:active{
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, 0.3);
}

.Button:disabled{
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}

.error{
    background: rgba(240, 87, 108, 1);
}

.success{
    background: rgba(161, 240, 69, 1);
    color: #000;
}

.primary{
    background: #2884f6;
    color: #fff;
}