.FinishedQuiz{
    padding: 20px;
    color: #fff;
    border: 2px solid #fff;
    box-sizing: border-box;
    border-radius: 5px;
    margin: 0 10px;
}

.FinishedQuiz ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

/* .FinishedQuiz ul li{

} */

.FinishedQuiz ul li i{
    margin-left: 10px;
}

.success{
    color: rgba(161, 240, 69, .7);

}


.error{
    color: rgba(240, 87, 108, .7);
}