.Auth{
    display: flex;
    justify-content: center;
    /* flex-direction: column; */
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(270deg, #cef2fa 0%, #59bfef 100%);
}
.Auth > div{
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
}

.Auth h1{
    color: #fff;
    text-align: center;
}

.AuthForm{
    padding: 20px;
    background: #eee;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
    border-radius: 5px;
}
